import React from "react";
import styled from "styled-components";
import "./youtube.css";
import { imgUrl } from "../../../config";

export const InstaIcon = styled.img`
  mix-blend-mode: color-dodge;
  margin-left: 20px;
  width: 65px;
  height: 60px;
  @media screen and (max-width: 650px) {
    height: 45px;
    width: 50px;
  }
`;

const Youtube = () => {
  return (
    <div className="youtube">
      <div className="thumbnail">
        <iframe
          width="841"
          height="502"
          src="https://www.youtube.com/embed/zD3Rd_7q3wc?controls=0"
          title="YouTube video player"
          frameborder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></iframe>
        <div className="bottom">
          <div className="text">
            <h1 className="title">Sonabyss_2k22 Trailer</h1>
            <span>Come Join Us</span>
          </div>
          <a
            href="https://youtu.be/zD3Rd_7q3wc"
            target="_blank"
            style={{ alignSelf: "center", margin: "0 auto" }}
          >
            <InstaIcon
              src={imgUrl + "/youtube-logo.svg"}
              className="youtube_ico"
            />
          </a>
        </div>
      </div>
    </div>
  );
};

export default Youtube;
