import styled from 'styled-components';

export const CentralContainer = styled.div`
    background: #130912;
    width: 100%;
`

export const Heading = styled.div`
    font-size: 58px;
    font-family: "livingbynumbers";
    opacity: 1;
    position: relative;
    z-index: 3;
    padding-bottom: 10px;
`

export const ViewAllButton = styled.button`
    font-family: "livingbynumbers";
    padding: 10px 45px;
    border-radius: 30px;
    background: #FF461F;
    border: transparent;
    font-size: 23px;
    margin: 3vh 5.5% 0 0;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.25), 4.2px 3.5px 3px 3px #ff1b12;
    @media screen and (max-width: 650px) {
        margin: 4vh 5% 0 0;
    }
`

/* Clubs Section end */
/* About Section Start */

export const Container = styled.div`
    width: 100%;
    color: white;
    margin: 0 auto;
    padding: 60px 0;
    margin-bottom: 60px;
`

export const AboutDiv = styled.div`
    width: 95%;
    padding: 80px 20px 0px 20px;
    margin: 0 auto;
    position: relative;
    border-radius: 50px;
    text-align: center;
    min-height: 600px;
    opacity: 1;
    @media screen and (max-width: 650px) {
        width: 100%;
        border-radius:0;
        /* padding: 80px 0px 0px 0px; 
         display: none; */
         padding: 80px 0px 0px 0px;
    }

    /* box-shadow: -1px 1px 0px 26px rgba(52,48,173,1); */

    &::after {
    content: '';
    width: 108%;
    height: 108%;
    position: absolute;
    top: 0;
    left: -4%;
    filter: blur(20px);
    border-radius: 50px;
    background: #3e1c33c2;
    @media screen and (max-width: 650px) {
        border-radius: 0;
        box-shadow: -1px 1px 0px 24px #0f070c;
        width: 120%;
        height: 108%;
    }
}
`

export const AboutDivContent = styled.div`
    position: relative;
    z-index: 2;
    height: 90%;
    width: 100%;
    text-align: center;
    
`

export const AboutText = styled.div`
    width: 92%;
    background: rgba(22, 10, 19, 0.7);
    font-size: 26px;
    margin: 0 auto;
    font-family: 'Abyssinica SIL';
    letter-spacing: 1.2px;
    padding: 30px 10% 30px 10%;
    line-height: 28px;
    border-radius: 50px;
    font-weight: 400;
    /* margin-top: -30px; */
    @media screen and (max-width: 650px) {
        padding: 40px 20px 50px 20px;
        line-height: 20px;
        border-radius: 0;
        font-size: 18px;
        width: 100%;
    }
`

export const ImageContainer = styled.div`
    height: 130%;
    width: 100%;
    max-height: 1000px;
    max-width: 1150px;
    position: absolute;
    top: 32%; right: 50%;
    mix-blend-mode: color-dodge;
    z-index: -1;
    transform: translate(50%,-50%);
    @media screen and (max-width: 650px) {
        /* width: 180%;
        max-height: 460px;
        left: -40%;
        top: 0;
        transform: translate(0,0); */
        display:none;
    }

`

export const Image = styled.img`
    height: 100%;
    width: 100%;
    opacity: 0.6;
    mix-blend-mode: color-dodge;
`

/* About Section end */
/* Sponsors Section Start */

export const SponserImg = styled.img`
    height: 100%;
    width: 100%;
    border-radius: 50%;
    /* flex-shrink: 0; */
`

export const SponserImgCont = styled.div`
    /* height: 240px;
    width: 250px; */
    margin-top: -7%;
    height: 13.3vw;
    width: 23%;
    max-height: 240px;
    position: relative;
    /* border: 2px solid green; */
    border-radius: 50%;
    flex-shrink: 0;
    &::after{
        content: '';
        height: 101%;
        width: 101%;
        position: absolute;
        top: -1px;
        left: -1px;
        z-index: 5;
        border-radius: 50%;
        box-shadow: inset 0px 0px 11px 10px rgb(0 0 0 / 90%);
    }

    @media screen and (max-width: 650px) {
        width: 230px;
        height: 230px;
    }
`

export const SponserName = styled.p`
    /* background-color: pink; */
    /* border: 2px solid pink; */
    /* margin-top: -2px; */
    /* height: 20%; */
    font-family: "bloodsuckers";
    font-style: normal;
    font-weight: 400;
    letter-spacing: 3px;
    font-size: 29px;
`


export const SponserImgMainContainer = styled.div`
    width: 92%;
    height: 450px;
    position: relative;
    background: rgba(22, 10, 19, 0.7);
    margin: 0 auto;
    padding: 20px 10%;
    overflow: hidden;
    border-radius: 50px;
    @media screen and (max-width: 650px) {
        border-radius: 0;
        width: 100%;
        padding: 0 5px 0 10px ;
    } 
`

export const SponserHolder = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    width: 100%;
    height: 100%;
    @media screen and (max-width: 650px) {
        border-radius: 0;
        gap: 10%;
        overflow-x: scroll;
        padding-bottom: 50px;
    } 
    @keyframes scrolling {
        0% {left:780px;}
        100% {left: -810px;}
  }
`

/* Sponsors Section end */
/* Footer Section Start */

export const HR = styled.hr`
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(255, 255, 255, 0.75), rgba(0, 0, 0, 0));
    margin: 0 auto;
    width: 1220px; 
`

export const Footer = styled.div`
    height: 100px;
    display : flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    background-color: #170b16;
    margin: 0 auto;
`
export const IconContainers = styled.div`
    display: flex;
    margin-left: 7%;
`

export const TextContainer = styled.div`
    margin-right: 12%;
    @media screen and (max-width: 650px) {
        margin-right: 2%;
    }
`

export const Dummy = styled.div`
    @media screen and (max-width: 650px) {display: none;}
`
export const IconCont = styled.a`
    display: block;
    margin-left: 30px;
    width: 55px;
    height: 55px;
    cursor: pointer;
    @media screen and (max-width: 650px) {
        height: 43px;
        width: 53px;
        margin-left: 5px;
    }
    @media screen and (max-width: 450px) {
        height: 40px;
        width: 45px;
        margin-left: 10px;
    }
`

export const InstaIcon = styled.img`
    mix-blend-mode: color-dodge;
    width: 100%;
    height: 100%;
    /* margin-left: 20px;
    width: 65px;
    height: 60px;
    @media screen and (max-width: 650px) {
        height: 45px;
        width: 50px;
        margin-left: 5px;
    }
    @media screen and (max-width: 450px) {
        height: 35px;
        width: 40px;
        margin-left: 10px;
    } */
`

export const SonabyssIcon = styled.img`
    height: 40px;
    width: 125px;
    margin: 10px 0 -20px 0;
    @media screen and (max-width: 650px) {
        height: 32px;
        width: 110px;
    }
    @media screen and (max-width: 450px) {
        height: 30px;
        width: 100px;
    }
`

export const FooterText = styled.p`
    margin-left: -10px;
    /* font-family: "bloodsuckers"; */
    color: white;
    font-size: 13px;
    font-family: "sans serif";
    /* @media screen and (max-width: 650px) {
        margin-left: 10px;
    } */
`

export const AboutUs = styled.span`
    color: white;
    font-size: 16px;
    transition: all 0.2s ease-in-out;
    &:hover {
        color: gray;
    };
    @media screen and (max-width: 650px) {
        font-size: 12px;
    }
`

