export default function CustomizedSwitches({ checked, setChecked }) {
  return (
    <MaterialUISwitch
      sx={{ m: 1 }}
      checked={checked}
      onChange={(e) => {
        setChecked(e.target.checked);
      }}
    />
  );
}
